<template>
    <div class="container">
        <h2 class="page-header"><i18n path="common.install"></i18n></h2>
        <hr class="my-2">
        <p><i18n path="common.guide.useNpm"></i18n></p>
        <pre class="brush:bash">
            npm i v-gallery --save
        </pre>
        <p><i18n path="common.guide.include"></i18n></p>
        <pre class="brush:js">
            import Vue from 'vue'
            import vGallery from 'v-gallery';
            Vue.use(vGallery);
        </pre>
        <br><br>


        <h2 class="page-header"><i18n path="common.deploy"></i18n></h2>
        <hr class="my-2">
        <script type="syntaxhighlighter" class="brush:html">
        <![CDATA[
            <template>
                <!-- default gallery mode -->
                <v-gallery :images="list"  >
                </v-gallery>
            </template>

            < script >
                export default {
                    data(){
                        return {
                            list: [
                                {title:'Image1',url:'http://xxx.com/1.jpg'},
                                {title:'Image2',url:'http://xxx.com/2.jpg'},
                                {...}
                            ]
                        }
                    }
                };
            < /script >
        ]]>
        </script>
        <br><br>

        <h2 class="page-header"><i18n path="common.example"></i18n></h2>
        <hr class="my-2">


        <h5><i18n path="gallery.demo.carousel"></i18n></h5>
        <p>
            <v-gallery type="carousel" :images="list" ></v-gallery>
        </p>
        <showcode>
            <script type="syntaxhighlighter" class="brush:html;gutter: false;">
            <![CDATA[
            <v-gallery type="carousel" :images="list" ></v-gallery>
            ]]>
            </script>
        </showcode>
        <br><br><br><br>

        <h5><i18n path="gallery.demo.galleryLight"></i18n></h5>
        <p>
            <v-gallery :images="list":caption="true" ></v-gallery>
        </p>
        <showcode>
            <script type="syntaxhighlighter" class="brush:html;gutter: false;">
            <![CDATA[
            <v-gallery :images="list":caption="true" ></v-gallery>
            ]]>
            </script>
        </showcode>
        <br><br><br><br>

        <h5><i18n path="gallery.demo.galleryDark"></i18n></h5>
        <p>
            <v-gallery :images="list" :dark="true" :caption="true" ></v-gallery>
        </p>
        <showcode>
            <script type="syntaxhighlighter" class="brush:html;gutter: false;">
            <![CDATA[
            <v-gallery :images="list" :dark="true" :caption="true" ></v-gallery>
            ]]>
            </script>
        </showcode>
        <br><br><br><br>

        <h5><i18n path="gallery.demo.galleryCustom"></i18n></h5>
        <v-gallery :images="list" class="image-box">
            <a href="javascript:void(0);"
               :data-image="img.url"
               :title="img.title"
               v-for="img in list" >
                <div class="bgbox">
                    <img :src="img.url">
                </div>
                <div class="img-title" v-html="img.title"></div>
            </a>
        </v-gallery>
        <showcode>
            <script type="syntaxhighlighter" class="brush:html;gutter: false;">
            <![CDATA[
            <template>
                <v-gallery :images="list" class="image-box">
                    <a href="javascript:void(0);"
                       :data-image="img.url"
                       :title="img.title"
                       v-for="img in list" >
                        <div class="bgbox">
                            <img :src="img.url">
                        </div>
                        <div class="img-title" v-html="img.title"></div>
                    </a>
                </v-gallery>
            </template>
            <style lang="scss" scoped>
                .image-box{
                    a {
                        clear: both;
                        display: inline-block;
                        margin: 0 10px 10px 0;
                        position: relative;
                        text-align: center;
                        .bgbox {
                            background-color: #232323;
                            height: 150px;
                            display:table-cell;
                            vertical-align:middle;
                            padding: 4px;
                            border-radius: 2px;
                            img{ width: 200px;display: block;}
                        }
                        .img-title {
                            bottom: 5px;
                            display: block;
                            text-align: center;
                            color: #999999;
                            padding-top: 5px;
                        }
                        &:hover {
                            .img-title { color: #232323; }
                        }
                    }
                }
            </style>
            ]]>
            </script>
        </showcode>
        <br><br><br><br>







        <h2 class="page-header"><i18n path="common.options"></i18n></h2>
        <table class="table table-bordered table-striped option-table">
