                <tr>
                    <th><i18n path="common.tbTitle.name"></i18n></th>
                    <th><i18n path="common.tbTitle.type"></i18n></th>
                    <th><i18n path="common.tbTitle.desc"></i18n></th>
                    <th><i18n path="common.tbTitle.required"></i18n></th>
                    <th><i18n path="common.tbTitle.default"></i18n></th>
                </tr>
            </thead>
            <tbody>
                <tr>
                    <td>type</td>
                    <td>String</td>
                    <td>
                        <p><i18n path="gallery.option.type"></i18n></p>
                        <ul>
                            <li><code>gallery</code></li>
                            <li><code>carousel</code></li>
                        </ul>
                    </td>
                    <td>false</td>
                    <td>gallery</td>
                </tr>
                <tr>
                    <td>images</td>
                    <td>Array</td>
                    <td>
                        <p><i18n path="gallery.option.images"></i18n></p>
                        <script type="syntaxhighlighter" class="brush:js;gutter: false;">
                        <![CDATA[
                        [
                            {
                                title: 'img1',
                                url:'http://image_url',
                                thumbnail:'http://image_thumbnail'
                            },
                            {...}
                        ]
                        ]]>
                        </script>
                    </td>
                    <td>true</td>
                    <td></td>
                </tr>
                <tr>
                    <td>control-pause</td>
                    <td>Boolean</td>
                    <td><i18n path="gallery.option.pause"></i18n></td>
                    <td>false</td>
                    <td>false</td>
                </tr>
                <tr>
                    <td>control-title</td>
                    <td>Boolean</td>
                    <td><i18n path="gallery.option.title"></i18n></td>
                    <td>false</td>
                    <td>true</td>
                </tr>
                <tr>
                    <td>dark</td>
                    <td>Boolean</td>
                    <td><i18n path="gallery.option.dark"></i18n></td>
                    <td>false</td>
                    <td>false</td>
                </tr>
                <tr>
                    <td>caption</td>
                    <td>Boolean</td>
                    <td><i18n path="gallery.option.caption"></i18n></td>
                    <td>false</td>
                    <td>false</td>
                </tr>
            </tbody>
        </table>
    </div>
</template>

<script>
    export default {
        data(){
            return {
                list: [
                    {url: require('../../assets/gallery/1.jpg'), title:'image1'},
                    {url: require('../../assets/gallery/2.jpg'), title:'image2'},
                    {url: require('../../assets/gallery/3.jpg'), title:'image3'},
                    {url: require('../../assets/gallery/4.jpg'), title:'image4'},
                    {url: require('../../assets/gallery/5.jpg'), title:'image5'},
                    {url: require('../../assets/gallery/6.jpg'), title:'image6'},
                    {url: require('../../assets/gallery/7.jpg'), title:'image7'},
                    {url: require('../../assets/gallery/8.jpg'), title:'image8'},
                    {url: require('../../assets/gallery/9.jpg'), title:'image9'},
                    {url: require('../../assets/gallery/10.jpg'), title:'image10'}
                ]
            };
        },
        methods: {
            resultFormat(row){
                if(row){
                    return row.name + ' (' + row.desc + ')';
                }
            }
        },
        mounted(){
            SyntaxHighlighter.highlight();
        }
    }
</script>

<style lang="scss" scoped>
    .image-box{
        /*border: 1px solid #DDDDDD;*/
        /*padding: 5px;*/

        a {
            clear: both;
            display: inline-block;
            margin: 0 10px 10px 0;
            position: relative;
            text-align: center;
            .bgbox {
                background-color: #232323;
                height: 150px;
                display:table-cell;
                vertical-align:middle;
                padding: 4px;
                border-radius: 2px;
                img{
                    width: 200px;
                    display: block;
                }
            }
            .img-title {
                bottom: 5px;
                display: block;
                text-align: center;
                color: #999999;
                padding-top: 5px;
            }
            &:hover {
                .img-title { color: #232323; }
            }
